@import url(https://use.typekit.net/gni0sjr.css);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'PanAm';
  src: url("/PanAmTextCapsNew.ttf") format("truetype"); }

html {
  background-color: #f49e4e; }

body {
  font-family: interstate, sans-serif;
  font-weight: 400;
  font-style: normal; }

#root {
  position: absolute;
  height: 100vh;
  width: 100vw; }

li {
  list-style-type: none;
  padding: 10px;
  margin: 0;
  display: inline-block;
  text-decoration: none; }

a {
  transition: color 1s ease;
  color: #fbe0c5;
  text-decoration: none; }

a:hover {
  color: white;
  text-decoration: none; }

ul {
  margin: 0; }

.icon-bar {
  background-color: #fbe0c5;
  height: 4px;
  margin: 6px 0;
  transition: 0.4s; }

::-moz-selection {
  background-color: #f49e4e;
  color: #fbe0c5; }

::selection {
  background-color: #f49e4e;
  color: #fbe0c5; }

textarea, select, input, button {
  outline: none; }

input {
  background-color: #154565 !important;
  border: none;
  margin-right: 10px;
  color: rgba(255, 255, 255, 0.7);
  width: 300px;
  height: 50px;
  border-radius: 5px;
  padding: 10px; }

@media (max-width: 375px) {
  h1 {
    font-size: 1.95rem; } }

.react-datepicker {
  font-family: interstate !important;
  background-color: #1d5c87 !important;
  color: rgba(255, 255, 255, 0.7) !important;
  border: none !important;
  box-shadow: 5px 7px 30px rgba(13, 43, 63, 0.6); }

.react-datepicker__day, .react-datepicker__day-name, .react-datepicker__current-month {
  color: rgba(255, 255, 255, 0.7) !important; }

.react-datepicker__day:hover {
  background-color: #1a547a !important; }

.react-datepicker__day--selected {
  background-color: #154565 !important;
  border-radius: 0.3rem; }

.react-datepicker__header {
  color: rgba(255, 255, 255, 0.7) !important;
  background-color: #154565 !important;
  border: none !important; }

.react-datepicker-popper[data-placement^="bottom"] .react-datepicker__triangle, .react-datepicker-popper[data-placement^="bottom"] .react-datepicker__triangle::before {
  border-bottom-color: #154565 !important; }

.react-datepicker__navigation--next {
  border-left-color: rgba(255, 255, 255, 0.7) !important; }

.react-datepicker__navigation--previous {
  border-right-color: rgba(255, 255, 255, 0.7) !important; }

.lds-ellipsis {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px; }

.lds-ellipsis div {
  position: absolute;
  top: 27px;
  width: 11px;
  height: 11px;
  border-radius: 50%;
  background: #fff;
  -webkit-animation-timing-function: cubic-bezier(0, 1, 1, 0);
          animation-timing-function: cubic-bezier(0, 1, 1, 0); }

.lds-ellipsis div:nth-child(1) {
  left: 6px;
  -webkit-animation: lds-ellipsis1 0.6s infinite;
          animation: lds-ellipsis1 0.6s infinite; }

.lds-ellipsis div:nth-child(2) {
  left: 6px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite; }

.lds-ellipsis div:nth-child(3) {
  left: 26px;
  -webkit-animation: lds-ellipsis2 0.6s infinite;
          animation: lds-ellipsis2 0.6s infinite; }

.lds-ellipsis div:nth-child(4) {
  left: 45px;
  -webkit-animation: lds-ellipsis3 0.6s infinite;
          animation: lds-ellipsis3 0.6s infinite; }

@-webkit-keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@keyframes lds-ellipsis1 {
  0% {
    -webkit-transform: scale(0);
            transform: scale(0); }
  100% {
    -webkit-transform: scale(1);
            transform: scale(1); } }

@-webkit-keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@keyframes lds-ellipsis3 {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1); }
  100% {
    -webkit-transform: scale(0);
            transform: scale(0); } }

@-webkit-keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

@keyframes lds-ellipsis2 {
  0% {
    -webkit-transform: translate(0, 0);
            transform: translate(0, 0); }
  100% {
    -webkit-transform: translate(19px, 0);
            transform: translate(19px, 0); } }

::-webkit-input-placeholder {
  color: rgba(255, 255, 255, 0.4); }

::-ms-input-placeholder {
  color: rgba(255, 255, 255, 0.4); }

::placeholder {
  color: rgba(255, 255, 255, 0.4); }

